body {
  background-color: #b8d3ea;
  padding: 20px;
}

.search {
  justify-content: center;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.form-text {
  min-width: 100px;
  margin-right: 5px;
  padding: 10px;
  font-size: 18px;
}

.form-select {
  padding: 10px;
}

.search-btn {
  background-color: #fff;
  border: 1px solid;
  border-radius: 4px;
  margin-left: 5px;
  padding: 10px;
  font-size: 18px;
  transition: background-color .25s, color .25s;
}

.search-btn:hover, .search-btn:focus {
  color: #00f;
  background-color: #ff0;
}

.weather-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  list-style: none;
  display: flex;
}

.weather-list .item {
  border: 1px solid green;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  display: flex;
}

